body {
    font-family: 'Kanit', sans-serif;
  }
  
  .container {
    padding: 40px 100px;
  }
  
  .title {
    margin: 0 0 40px;
    text-align: center;
    color: #0080ff;
  }
  
  .gamebox {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin: 0 auto;
    width: 1055px;
    height: 790px;
  }
  
  .line {
    display: flex;
    flex-direction: row;
    gap: 5px;
  }
  
  .flip-card {
    background-color: transparent;
    width: 260px;
    height: 260px;
    perspective: 1000px;
    box-sizing: border-box;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }
  
  
  .flip-card-front,
  .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    border: 1px solid black;
    border-radius: 6px;
    cursor: pointer;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
  }
  
  .flip-card-front {
    background-color: white;
  }
  
  .flip-card-back {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #cdefff;
    background-position: center;
    background-size: 180px;
    background-repeat: no-repeat;
    transform: rotateY(180deg);
  }
  
  .image {
    width: 180px;
    height: 180px;
  }
  
  .footer {
    text-align: center;
  }
  
  .copyright {
    margin: 50px 0 0;
    display: inline-block;
  }
  
