body {
  font-family: Kanit, sans-serif;
}

.container {
  padding: 40px 100px;
}

.title {
  text-align: center;
  color: #0080ff;
  margin: 0 0 40px;
}

.gamebox {
  width: 1055px;
  height: 790px;
  flex-direction: column;
  gap: 5px;
  margin: 0 auto;
  display: flex;
}

.line {
  flex-direction: row;
  gap: 5px;
  display: flex;
}

.flip-card {
  width: 260px;
  height: 260px;
  perspective: 1000px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0);
}

.flip-card-inner {
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  transition: transform .8s;
  position: relative;
}

.flip-card-front, .flip-card-back {
  width: 100%;
  height: 100%;
  cursor: pointer;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border: 1px solid #000;
  border-radius: 6px;
  position: absolute;
}

.flip-card-front {
  background-color: #fff;
}

.flip-card-back {
  background-color: #cdefff;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 180px;
  justify-content: center;
  align-items: center;
  display: flex;
  transform: rotateY(180deg);
}

.image {
  width: 180px;
  height: 180px;
}

.footer {
  text-align: center;
}

.copyright {
  margin: 50px 0 0;
  display: inline-block;
}

